.name-container {
    position: relative;
    width: 110px;
    margin: 110px auto 0 auto;
    -webkit-transition: -webkit-transform .4s ease-in-out;
    transition: -webkit-transform .4s ease-in-out;
    -o-transition: transform .4s ease-in-out;
    transition: transform .4s ease-in-out;
    transition: transform .4s ease-in-out, -webkit-transform .4s ease-in-out
}
.short-intro {
    position: relative;
    margin: 80px auto 0 auto;
    text-align: center;
    opacity: 1;
    font-family: Montserrat, sans-serif;
    -webkit-transition: opacity 1s ease-in-out, -webkit-transform .4s ease-in-out;
    transition: opacity 1s ease-in-out, -webkit-transform .4s ease-in-out;
    -o-transition: transform .4s ease-in-out, opacity 1s ease-in-out;
    transition: transform .4s ease-in-out, opacity 1s ease-in-out;
    transition: transform .4s ease-in-out, opacity 1s ease-in-out, -webkit-transform .4s ease-in-out;
    padding: 0 20px
}
.left-cover,
.right-cover {
    position: absolute;
    height: 60px;
    width: 60px;
    background-color: transparent;
    top: 0;
    left: 0;
    border-color: #2c65a0;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out
}
.right-cover {
    left: 54px;
    top: 4px;
    border-right: 6px solid orange;
    border-bottom: 6px solid orange;
    -webkit-animation-name: downright;
            animation-name: downright
}
.left-cover {
    left: -10px;
    top: -10px;
    border-left: 6px solid orange;
    border-top: 6px solid orange;
    -webkit-animation-name: upleft;
            animation-name: upleft
}
@-webkit-keyframes upleft {
    0% {
        -webkit-transform: translate3d(0);
                transform: translate3d(0)
    }
    to {
        -webkit-transform: translate3d(-15px, -15px, 0);
                transform: translate3d(-15px, -15px, 0)
    }
}
@keyframes upleft {
    0% {
        -webkit-transform: translate3d(0);
                transform: translate3d(0)
    }
    to {
        -webkit-transform: translate3d(-15px, -15px, 0);
                transform: translate3d(-15px, -15px, 0)
    }
}
@-webkit-keyframes downright {
    0% {
        -webkit-transform: translate3d(0);
                transform: translate3d(0)
    }
    to {
        -webkit-transform: translate3d(15px, 15px, 0);
                transform: translate3d(15px, 15px, 0)
    }
}
@keyframes downright {
    0% {
        -webkit-transform: translate3d(0);
                transform: translate3d(0)
    }
    to {
        -webkit-transform: translate3d(15px, 15px, 0);
                transform: translate3d(15px, 15px, 0)
    }
}

.name {
    font-size: 28px;
    line-height: 60px;
    text-align: center;
    color: #505050;
    font-family: Montserrat, sans-serif;
    opacity: 0;
    -webkit-animation: name_fadein ease-in-out;
            animation: name_fadein ease-in-out;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    line-height: 60px;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}

@-webkit-keyframes name_fadein {
    0% { opacity: 0 }
    to { opacity: 1 }
}

@keyframes name_fadein {
    0% { opacity: 0 }
    to { opacity: 1 }
}



